.main-content
    background-color: #ffffff
    box-shadow: 1px 1px 13px 0px rgba(0,0,0,0.14)
    padding: 15px
    border-radius: 15px
    &__title
        font-size: 32px
        color: var(--main-text-color)
        margin-bottom: 1em
        margin-top: 0.2em
        font-weight: 500
        font-family: var(--title-font)
    &--subjects
        display: grid
        grid-template-columns: 1fr 1fr 1fr
        gap: 20px
    &--educators
        display: grid
        grid-template-columns: 1fr 1fr 1fr 
        gap: 20px
    &--news
        display: flex
        flex-wrap: wrap
        box-sizing: border-box
        gap: 20px
        & > *
            width: calc(25% - 20px * 3 / 4)
                

                